import React, { useContext } from "react";
import classes from "./favourite.module.css";
import { GlobalProvider, GlobalState } from "../../Context/globalState";
import FilmCard from "../../Components/film card/filmCard";

const Favourite = (props) => {
  const { watchList } = useContext(GlobalState);

  return (
    <GlobalProvider>
      <div className={classes.favourite}>
        <h2>
          Watch List <span> {watchList.length} Movies </span>
        </h2>
        <div className={classes.cards}>
          {watchList ? (
            watchList.map((elem) => {
              return <FilmCard movie={elem} type="favourite" />;
            })
          ) : (
            <div> Watch List is Empty.</div>
          )}
        </div>
      </div>
    </GlobalProvider>
  );
};

export default Favourite;
